import React, { useState, useEffect } from "react";
import { getvaluli } from "../js/main.js";
import "../css/pocket.css";
import video from "../video/ANTIM The Final Truth.mp4";
import up from "../Images/up.png";
import Down from "../Images/down.png";
import ReactPlayer from "react-player";

const DBSAdcard1 = (props) => {
  const [videoFilePath, setVideoFilePath] = useState(null);
  const handleVideoUpload = (event) => {
    document.getElementById("rwadx_tamil_pg_deal").style.display = "none";
    setVideoFilePath(URL.createObjectURL(event.target.files[0]));
  };

  const [data, setData] = useState({
    title: "",
    Video: "",
    description: "",
  });

  const InputEvent = (event) => {
    const { name, value } = event.target;

    setData((preVal) => {
      return {
        ...preVal,
        [name]: value,
      };
    });
  };

  const formSubmit = (e) => {
    e.preventDefault();
    alert(
      `Title ${data.title}. video ${data.Video}, description ${data.description}`
    );
  };
  function hideVideo() {
    document.getElementById("pocket-video-section").classList.add("fade");
    document
      .getElementById("pocket-video-section")
      .classList.remove("fadehide");
    setTimeout(function () {
      document.getElementById("ddlclick1").style.display = "none";
      document.getElementById("ddlclick2").style.display = "block";
      document
        .getElementById("bigger_int")
        .style.setProperty("background", "none", "important");
      const element = document.getElementById("major_nav");
      element.style.setProperty("height", "39px", "important");
      element.style.setProperty("top", "unset", "important");
    }, 1000);
    document.getElementById("rwadx_tamil_pg_deal").pause();
  }
  function showVideo() {
    document.getElementById("pocket-video-section").classList.remove("fade");
    document.getElementById("pocket-video-section").classList.add("fadehide");
    document
      .getElementById("major_nav")
      .style.setProperty("height", "240px", "important");
    document.getElementById("ddlclick2").style.display = "none";
    document.getElementById("ddlclick1").style.display = "block";
    document.getElementById("rwadx_tamil_pg_deal").play();
    // add code for video
  }
  return (
    <>
      <section className="navigation-sections navigation-section">
        <div className="container-fluid" style={{ padding: "0px" }}>
          <div className="col-md-12">
            <div className="row" style={{ height: "100vh" }}>
              <div
                className="col-md-7 col-lg-7"
                style={{ backgroundColor: "#fff" }}
              >
                <div className="row" id="">
                  <div className="col-lg-8 offset-lg-2 col-md-10">
                    <div className="form-section form-div">
                      <div id="pocketDetlform">
                        <div className="adstittle">
                          <h4>Image Interstitial / CREATE AD</h4>
                        </div>
                        <form onSubmit={formSubmit}>
                          <div className="form-group row">
                            <label
                              htmlFor="exampleFormControlInput1"
                              className="col-sm-4 col-form-label"
                            >
                              Title:
                            </label>
                            <div className="col-sm-8">
                              <input
                                type="text"
                                className="form-control"
                                id="exampleFormControlInput1"
                                name="title"
                                value={data.title}
                                onChange={InputEvent}
                                placeholder="Enter your name"
                              />
                            </div>
                          </div>
                          <div className="form-group row">
                            <label
                              htmlFor="exampleFormControlInput1"
                              className="col-sm-4 col-form-label"
                            >
                              Video Upload:
                            </label>
                            <div className="col-sm-8">
                              <input
                                type="file"
                                className="form-control"
                                onChange={handleVideoUpload}
                              />
                            </div>
                          </div>

                          <div className="form-group row">
                            <label
                              htmlFor="exampleFormControlTextarea1"
                              className="col-sm-4 col-form-label"
                            >
                              Description:
                            </label>
                            <div className="col-sm-8">
                              <textarea
                                className="form-control"
                                id="exampleFormControlTextarea1"
                                rows="3"
                                name="description"
                                value={data.description}
                                onChange={InputEvent}
                              ></textarea>
                            </div>
                          </div>

                          <div className="col-md-12 col-lg-12">
                            <div style={{ textAlign: "right" }}>
                              <button
                                className="btn btn-outline-primary"
                                type="submit"
                              >
                                Submit form
                              </button>
                            </div>
                          </div>
                        </form>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div
                className="col-md-5 col-lg-5"
                style={{ backgroundColor: "#087ccd" }}
              >
                <div className="form-section form-div right-content-section">
                  <h4>Interstitial Ads</h4>
                  <p>
                    Lorem ipsum dolor sit amet . The graphic and typographic
                    operators know this well,
                  </p>
                  <button type="submit" className="btn bg-success text-white">
                    Generate Code
                  </button>
                </div>
              </div>
              <div className="smartphone smartphone-section">
                <div className="content">
                  <div className="video-section">
                    <div className="bigger_int" id="bigger_int">
                      <div className="inner_main">
                        <div className="major_nav" id="major_nav">
                          <a
                            className="btn-primary"
                            id="ddlclick1"
                            onClick={hideVideo}
                          >
                            <div className="pocket-main-title">
                              <div className="marquee">
                                <div>
                                  {/* This is sort circut concept */}
                                  {data.title || "ANTIM The Final Truth"}
                                </div>
                              </div>
                              <div>
                                <img
                                  src={Down}
                                  alt=""
                                  width="18px"
                                  height="18px"
                                />
                              </div>
                            </div>
                          </a>
                          <a
                            className="btn-primary"
                            id="ddlclick2"
                            onClick={showVideo}
                            style={{ display: "none" }}
                          >
                            <div className="pocket-main-title">
                              <div className="marquee">
                                <div>
                                  {data.title || "ANTIM The Final Truth"}
                                </div>
                              </div>
                              <div>
                                <img
                                  src={up}
                                  alt=""
                                  width="18px"
                                  height="18px"
                                />
                              </div>
                            </div>
                          </a>

                          <div id="pocket-video-section">
                            <div
                              className="video-player-container"
                              id="apvvideo-player-container"
                            >
                              <video
                                id="rwadx_tamil_pg_deal"
                                controls
                                autoPlay
                                muted
                              >
                                <source src={video} type="video/mp4" />
                              </video>
                              <ReactPlayer
                                url={videoFilePath}
                                width="100%"
                                height="100%"
                                controls={true}
                                className="video-tag"
                                playing
                                accept="video/mp4,video/x-m4v,video/avi,video/*"
                              />

                              <div className="pocket-title-set ">
                                <div>
                                  <p style={{ padding: "0px 10px" }}>
                                    <b>
                                      {data.description ||
                                        "ANTIM The Final Truth"}
                                    </b>
                                  </p>
                                </div>
                                <div className="btnshow">
                                  <a
                                    href="#"
                                    id="btnclick"
                                    className="btn btnclick"
                                    target="_blank"
                                  >
                                    Click Now!
                                  </a>
                                </div>
                              </div>
                            </div>

                            <a
                              href="https://www.primevideo.com/"
                              target="_blank"
                              className="am_ad_container"
                              id="apvam_ad_container"
                            ></a>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};
export default DBSAdcard1;
