import React from "react";
import { NavLink } from "react-router-dom";

const HeaderAd = () => {
  const DBSlink = "https://www.rwadx.com/digital-brand-solutions/";
  return (
    <>
      <div className="container-fluid nav_bg section-box header-section fixed-top header-fix">
        <div className="row">
          <div className="col-12 p-0">
            <nav className="navbar navbar-expand-lg navbar-light bg-white">
              <div className="container-fluid">
                <NavLink className="navbar-brand" to="/">
                  <img
                    src="https://www.rwadx.com/digital-brand-solutions/img/logo_black.png"
                    style={{ width: "125px" }}
                  />
                </NavLink>
                <button
                  className="navbar-toggler"
                  type="button"
                  data-toggle="collapse"
                  data-target="#navbarSupportedContent"
                  aria-controls="navbarSupportedContent"
                  aria-expanded="false"
                  aria-label="Toggle navigation"
                >
                  <span className="navbar-toggler-icon"></span>
                </button>
                <div
                  className="collapse navbar-collapse"
                  id="navbarSupportedContent"
                >
                  <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                    <li
                      className="nav-item"
                      style={{ backgroundColor: "#20df80" }}
                    >
                      <NavLink
                        className="nav-link menu_active active"
                        aria-current="page"
                        style={{ color: "#fff" }}
                        exact="true"
                        to="/"
                      >
                        Home
                      </NavLink>
                    </li>

                    <li className="nav-item">
                      <a className="nav-link" href={DBSlink}>
                        DBS
                      </a>
                    </li>
                    <li className="nav-item">
                      <a
                        className="nav-link"
                        href="https://calendly.com/rwadx-sales/30min?month=2022-01"
                      >
                        Contact Us
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
            </nav>
          </div>
        </div>
      </div>
    </>
  );
};
export default HeaderAd;
