import React, { useState } from 'react'
import { NavLink } from "react-router-dom";

const Card = (props) => {

  const [navclass, setnavclass] = useState();
  function funcard(id){
   console.log(id);
  props.setCurrentElement(id);
  }

 function adsCard(id){
  var getlitag= document.getElementsByClassName('btnbg');
  console.log(getlitag)
   for (let index = 0; index < getlitag.length; index++) {
    // const element = getlitag[index].removeAttribute('class');
     const element=getlitag[index].classList.remove("btnbg");
   }
   setnavclass('btnbg');
 };
 
  return (
    <>
      <div className="col-md-6 col-12 mx-auto mb-4">

  <div className="adcard-list">
  <NavLink to={`${props.visit}/${props.id}`}   htmlFor={props.id}>
  <input type="radio" id={props.id} name="select" value="2"/>
  <label htmlFor={props.id}  onClick={()=>{adsCard(props.id)}} >
<div className="card-content">
<div className="text-section">
    <h5>{props.title}</h5>
    <p>
    Lorem ipsum dolor sit amet . The graphic and typographic operators know this well, in reality all the professions dealing with the universe of communication have a stable relationship with these words
    </p>
    <NavLink to={`${props.visit}/${props.id}`} id="btnselect"  htmlFor={props.id} className={['btnselect','btn',navclass].join(' ')}   onClick={()=>{funcard(props.id)}}>
    Select Now
    </NavLink>
  </div>
  <div className="image-section">
  <img src={props.imgsrc} className="card-img-top" alt={props.imgsrc} />
  </div>
</div>
  </label>
  </NavLink>
 
</div>
      </div>
    </>
  );
};

export default Card;