import React,{useState} from 'react';
import Sdata from '../ArrayOfCard/Sdata';
import Card from './Card';
import {getvaluli} from '../js/main.js';
const DBSHome=()=>{
const getallfunction={
  getvaluli
}

  const [currentElement,setCurrentElement]= useState("");
 
    return(
        <>
          <section className="home-navigation-sections">
       
          <div className="container-fluid mb-5">
            <div className="row">
              <div className="col-10 mx-auto">
                <div className="row gy-4 listcard">
                  {Sdata.map((val, ind) => {
                 
                    return <Card setCurrentElement={setCurrentElement} visit={val.url} key={ind} imgsrc={val.imgsrc}  title={val.title} id={val.id} btnfun={val.clickbtn}/>;
                  })}
          
                </div>
              </div>
            </div>
          </div>
          </section>
        </>
    );
 
}
export default DBSHome
